@import 'colors';

.slideshow-container {
    grid-column: 1 / -1;
    background: $black;
    height: 92vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    overflow-y: hidden;
}
.slideshow-content {
    object-fit: cover;
    object-position: 50% 50%;
    width: 100vw;
    height: 94vh;
    z-index: -1;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1s;
  animation-name: fade;
  animation-duration: 1s;
}

@-webkit-keyframes fade {
  from {opacity: .5} 
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .5} 
  to {opacity: 1}
}
@media screen and (max-width: 600px) {
  .slideshow-container {
    height: 55vh;
  }
  .slideshow-content {
    height: 55vh;
  }
}